<template>
    <div class="auth login">
        <div class="wrapper">
            <div class="reg-form">
                <div class="img">
                    <img alt="site logo" src="../assets/big-logo.png">
                </div>

                <input v-on:keyup="this.touch.login = true" :class="{wrong : this.value.login.match(/[\w]+/)?.length == null && this.touch.login}" type="text" v-model="this.value.login" placeholder="Логин или адрес электронной почты" required>
                <div class="error-message" v-if="this.value.login.match(/[\w]+/)?.length == null && this.touch.login"> Введите логин! </div>

                <input v-on:keyup="this.touch.password = true" :class="{wrong : this.value.password == '' && this.touch.password}" :type="type_passSeen" v-model="this.value.password" placeholder="Пароль" required> 
                
                <button class="visibility" v-if="!passSeen" @click="hide()"><img src="@/assets/eye.svg"></button>
                <button class="visibility" v-if="passSeen" @click="hide()"><img src="@/assets/eye-slash.svg"></button>
                
                <div class="error-message" v-if="this.value.password == '' && this.touch.password">Введите пароль!</div>

                <div class="loading-img" v-if="loading" ><img src="@/assets/loading.gif"></div>

                <button v-if="!loading" @click="auth()">Войти</button>

                <div class="error-message" v-if="this.exists">Пользователя с такими данными не существует</div>

                <p>Если у вас нет аккаунта вы можете <router-link to="/registration">зарегистрироваться</router-link></p>
            </div>
        </div>
    </div>
</template>
<script>
import Lib from '@/Library';

export default{
    data(){
        return{
            value : {
                login : '', 
                password : '', 
            },
            touch:{
                login : false,
                password : false
            },
            loading : false,
            passSeen : true,
            exists : false,
            type_passSeen : 'password',
        }
    },
    methods:{
        auth(){
            this.touch.login = true;
            this.touch.password = true;
            this.loading = true;
            this.exists = false;

            if(this.value.login.length > 0 && this.value.password.length > 0){                
                if(this.value.login.match(/[\w]+/)?.length != null && this.touch.login && this.value.password != '' && this.touch.password){
                    (async ()=>{
                        let api = new Lib.Api();
                        api = await api.login(this.value.login, this.value.password);
                        if(api.token == 0){
                            this.loading = false;
                            this.exists = true;
                        }else{
                            localStorage.setItem('token', api.token);
                            window.location.href = './documents';
                        }
                        console.log(api.token);
                    })();
                }
            }else{
                this.loading = false;
            }
        },
        hide(){
            this.$data.passSeen = !this.$data.passSeen;
            this.$data.type_passSeen = (this.$data.passSeen) ? 'password' : 'text';
        }
    }
}
</script>

<style scoped>
    .login{
        min-height: calc(100dvh - 310px);
        padding: 150px 0 50px 0;
        height: fit-content;
    }
    .loading-img img{
        width: 100%;
        height: 100%;
    }
    .loading-img{
        height: 70px;
        width: 70px;
        margin: 0 auto;
    }
</style>