<template>
    <div class="documents">
        <div class="row">
            <button class="delete" @click="exit">Выйти</button>
        </div>
        <h1>Мои документы</h1>
        <table v-if="!loading && list.length > 0">
            <thead>
                <tr>
                    <th>Название</th>
                    <th class="invisible">&nbsp;</th>
                    <th class="invisible">&nbsp;</th>
                    <th class="invisible">&nbsp;</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="i in list" :key="i.id">
                    <td>{{ i.name }}</td>
                    <td class="invisible"><button class="delete" @click="deleteDocument(i.id)">Удалить документ</button></td>
                    <td class="invisible"><button class="btn" @click="showDocument(i.id, i.name)">Открыть документ</button></td>
                    <td class="invisible"><button class="green" @click="downloadDocument(i.id, i.name)">Скачать документ</button></td>
                </tr>
            </tbody>
        </table>
        <div class="img" v-if="loading"><img src="@/assets/loading.gif"></div>
    </div> 
</template>
<script>
import Lib from '@/Library';
export default {
    data() {
        return {
            api: new Lib.Api(localStorage.token),
            list: [],
            docId: 0,
            doc: '',
            loading: true
        }
    },
    mounted() {
        this.update()
    },
    methods: {
        deleteDocument(id) {
            (async (id) => {
                this.loading = true;
                this.api.deleteDoc(id);
                await new Promise(r => setTimeout(r, 900));
                this.update();
            })(id)
        },
        showDocument(id, name) {
            (async (id, name) => {
                let document = await this.api.one(id);
                try {
                    if( JSON.parse(document)?.error == 'Invalid token'){
                        alert('Срок сессии истек!');
                        this.exit();
                    }
                } catch (error) {
                    localStorage.elementsData = Lib.xmlToFrontData(document);
                    localStorage.openedDocument = name;
                    window.location.href = './construct';
                }
                
            })(id, name)
        },
        downloadDocument(id, name){
            (async (id, name) => {
                let document = await this.api.one(id);
                Lib.downloadDocumentFromXML(document, name)
            })(id, name)
        },
        exit() {
            localStorage.clear();
            window.location.href = './login';
        },
        update(){
            (async () => {
                this.list = await this.api.list();
                this.loading = false;
            })();
        }
    }
}
</script>

<style scoped>
.documents input {
    min-width: 33%;
    width: fit-content;
    border: transparent;
    padding: 1px 3px;
    margin: 5px 10px;
    border-bottom: black solid 1px;
    transition: .4s;
    font-size: 16px;
}

pre {
    text-align: left;
    width: fit-content;
    margin: 0 auto;
}

.documents {
    width: 1280px;
    margin: 0 auto;
    margin-top: 40px;
}

table {
    border-collapse: collapse;
    width: 100%;
    margin-top: 30px;
}

table td {
    border: 1px solid black;
}

table td {
    text-align: left;
    padding: 10px;
}

table *.invisible {
    border-color: transparent;
    width: 100px;
}

table button {
    width: 100% !important;
    margin: 0 !important;
}
.img{
    width: 70px;
    height: 70px;
    margin: 20px auto;
}
.img img{
    width: 100%;
    height: 100%;
}
.row{
    width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: end;
}
</style>