<template>
    <div class="PartElement">
        <div class="header">
            <h2>Раздел</h2>
            <select v-model="content.type" @change="sendData()">
                <option>Большой штамп</option>
                <option>Малый штамп</option>
                <option selected>Пустая страница</option>
            </select>
        </div>
        <div class="content">
            <component 
            class="component"
            v-for="(el, index) in content.children"
            :parrent="content.children"
            :el="el.content"
            v-bind:key="index" 
            :is="el.component"
            @content="el.content = $event"
            ></component>
            <div class="add" @drop="onDrop($event, content.children)" @dragenter.prevent @dragover.prevent> + </div>
        </div>
        <div class="del">
            <button @click="deleteElement()" class="delete">Удалить</button>
        </div>
    </div>
</template>
  
<script>

import HeaderElement from "@/components/HeaderElement.vue";
import ImgElement from "@/components/ImgElement.vue";
import InfoAboutDocument from "@/components/InfoAboutDocument.vue";
import MaintanceElement from "@/components/MaintenanceElement.vue";
import ParagraphElement from "@/components/ParagraphElement.vue";
import TableElement from "@/components/TableElement.vue";

export default {
    name: 'PartElement',
    components: {
      HeaderElement,
      ImgElement,
      InfoAboutDocument,
      MaintanceElement,
      ParagraphElement,
      TableElement
    },
    data(){
        return{
            content: {
                type: (this.$props.el.type != undefined) ? this.$props.el.type : '',
                children: (this.$props.el.children != undefined) ? this.$props.el.children : [],
            }
        }
    },
    props:{
        parrent : Array,
        el: Object,
    },
    // setup(){
    //     function 

    //     return {
    //         sendData, onDrop
    //     }
    // },
    methods:{
        deleteElement(){
            this.$props.parrent.splice(this.$.vnode.key, 1)
        },    
        sendData(){
            this.$emit('content', this.content);
        }, 
        onDrop (event, list){
            let item = event.dataTransfer.getData('component');
            if(item != 'PartElement'){
                list.push( { component : item, content : {} } );
                this.sendData();
                // console.log(list);
            }
        }
    },
}
</script>

<style scoped>
.del {
    margin: 0 10px;
    margin-top: 10px;
    display: flex;
    justify-content: flex-end;
}
button.delete{
    margin-left: auto;
}

</style>