<template>
  <div class="form">
    <div class="about">
      <h1>Генератор</h1>
    </div>
    <p>1. Выберите вариант загружаемого исходного файла:</p>
    <select id="type">
      <option value="1">XML</option>
      <option value="2">ZIP</option>
    </select>
    <p>2. Выберите файл с устройства</p>
    <input type="file" id="file" ref="fileInput" @change="fileChange">
    <span v-if="errors.fileEmpty" class="error">Для начала надо выбрать корректный файл для загрузки!</span>
    <p>3. Отправьте на сервер для обработки</p>
    <div>
      <button @click="send()" class="btn">Обработать</button>
    </div>
  </div>
</template>

<script>
import Lib from '@/Library';
export default {
  name: 'GenerateView',
  data() {
    return {
      file: null,
      errors: {
        fileEmpty: false,
      }
    }
  },
  methods: {
    send() {
      if (this.file == null) {
        this.errors.fileEmpty = true;
      } else {
        this.errors.fileEmpty = false;
      }
      if (this.file) {
        var reader = new FileReader();
        reader.readAsText(this.file, "UTF-8");
        reader.onload = (evt) => {
          try{
            localStorage.elementsData = Lib.xmlToFrontData(evt.target.result);
            localStorage.openedDocument = this.file.name.split('.')[0];
            window.location.href = './construct';
          }catch(e){
            this.errors.fileEmpty = true;
          }
        }
        reader.onerror = () => {
          this.errors.fileEmpty = true;
        }
      }
    },
    fileChange() {
      this.file = this.$refs.fileInput.files[0];
    },
  },
}
</script>

<style>
#app .form p {
  margin: 15px 0 5px 0;
}

.form {
  width: 600px;
  margin: 0 auto;
  text-align: left;
}

#app span.error {
  color: red;
  display: block;
}
</style>