<template>
    <section >
        <div class="about">
            <h1>Конструктор</h1>
        </div>
        <main>
            <div class="table" @mousemove="save()">
                <component 
                    class="component" 
                    v-for="(el, index) in elementsData" 
                    :parrent="elementsData" 
                    :el="el.content"
                    v-bind:key="index" 
                    :is="el.component" 
                    @content="el.content = $event"
                ></component>
                <div class="add" @drop="onDrop($event, elementsData)" @dragenter.prevent @dragover.prevent> + </div>
            </div>
            <div class="elements">
                <div class="controls">
                    <button @click="printData()" class="btn" @contextmenu="ale()" oncontextmenu="return false;">Скачать документ</button>
                    <button @click="saveToServer()" class="btn" @contextmenu="clearConsole()" :disabled='check()' oncontextmenu="return false;">Сохранить в мои документы</button>
                    <button @click="save()" class="btn" @contextmenu="clearConsole()" oncontextmenu="return false;">Сохранить</button>
                    <button @click="clearField()" class="delete" @contextmenu="clearConsole()" oncontextmenu="return false;">Очистить конструктор</button>
                </div>
                <div class="tool" v-for="(el, i) in items" v-bind:key="i" draggable="true" @dragstart="startDrag($event, el)">{{
                    el.content }}</div>
            <!-- <button @click="show()" class="green">Вывод</button> -->
            </div>
        </main>
    </section>
</template>

<script>

import HeaderElement from "@/components/HeaderElement.vue";
import ImgElement from "@/components/ImgElement.vue";
import InfoAboutDocument from "@/components/InfoAboutDocument.vue";
import MaintanceElement from "@/components/MaintenanceElement.vue";
import ParagraphElement from "@/components/ParagraphElement.vue";
import PartElement from "@/components/PartElement.vue";
import TableElement from "@/components/TableElement.vue";
import Lib from '@/Library';
// import { ref } from "vue";

export default {
    name: 'ConstructView',
    mixins: [Lib],
    components: {
        HeaderElement,
        ImgElement,
        InfoAboutDocument,
        MaintanceElement,
        ParagraphElement,
        PartElement,
        TableElement
    },
    data() {
        return {
            elementsData: (localStorage?.elementsData != undefined) ? JSON.parse(localStorage?.elementsData) : [
                { component: "InfoAboutDocument", content: {} },
            ],
            items: [
                { content: 'Рамка страницы', name: 'PartElement' },
                { content: 'Заголовок', name: 'HeaderElement' },
                { content: 'Содержание', name: 'MaintanceElement' },
                { content: 'Абзац', name: 'ParagraphElement' },
                { content: 'Таблица', name: 'TableElement' },
                { content: 'Изображение', name: 'ImgElement' },
            ],
        };
    },
    setup() {
        function startDrag(event, item) {
            event.dataTransfer.dropEffect = 'move';
            event.dataTransfer.effectAllowed = 'move';
            event.dataTransfer.setData('component', item.name);
        }

        function onDrop(event, list) {
            let item = event.dataTransfer.getData('component');
            // console.log(item);
            list.push({ component: item, content: {} });
            // console.log(list);
        }

        return {
            startDrag, onDrop
        }
    },
    methods: {
        printData() {
            Lib.downloadDocumentFromXML(Lib.frontDataToXml([...this.$data.elementsData]), localStorage?.openedDocument || 'Document');
        },
        setContent(obj, data) {
            obj.content = data;
        },
        ale() {
            console.log(Lib.frontDataToXml([...this.$data.elementsData]));
            return false;
        },
        save() {
            localStorage.setItem('elementsData', JSON.stringify(this.$data.elementsData));
        },
        clearField() {
            localStorage.removeItem('openedDocument');
            this.$data.elementsData = [
                { component: "InfoAboutDocument", content: {} },
            ];
            localStorage.setItem('elementsData', JSON.stringify(this.$data.elementsData));
            window.location.href = './construct';
        },
        show(){
            console.log(JSON.stringify(this.$data.elementsData))
        },
        clearConsole(){
            console.clear();
        },
        saveToServer(){
            let api = new Lib.Api(localStorage.token);
            let name = '';
            if(localStorage.openedDocument === undefined){
                name = prompt('Введите название: ');
            }else{
                name = prompt('Введите название: ', localStorage.openedDocument);
            }
            if(name != null){
                if(name != ''){
                    api.save(name, 'xml', Lib.frontDataToXml([...this.$data.elementsData]));
                    localStorage.openedDocument = name;
                }else{
                    alert('Введите название!');
                    this.saveToServer();
                }
            }
        },
        check() {
            return localStorage.getItem('token') == undefined;
        }
    }
}

</script>

<style>
.tool {
    border: 1px solid black;
    color: black;
    opacity: 0.7;
    border-radius: 15px;
    background-color: rgb(240, 240, 240);
    min-height: 20px;
    min-width: 20px;
    padding: 20px;
    margin-bottom: 20px;
    display: block;
    cursor: grab;
    transition: .4s;
}
.tool:hover{
    opacity: 1;
    background-color: white;
}

main {
    margin: 20px 10% 0 10%;
    display: flex;
    height: 78vh;
}

.component {
    display: block;
    padding: 10px 0;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    border: gray solid 1px;
    border-radius: 10px;
    margin: 10px;
    margin-bottom: 20px;
}
.controls{
    padding-bottom: 10px;
}
.controls button{
    width: 100% !important;
}
.table {
    width: 100%;
    border: lightgray solid 1px;
    padding: 20px 0;
    margin-right: 20px;
    overflow: auto;
    padding: 10px;
}

.elements {
    padding: 0;
    width: 150px;
}

.add {
    display: block;
    background-color: lightgray;
    height: 60px;
    border-radius: 10px;
    border: gray 1px solid;
    font-size: 45px;
    color: gray;
    text-align: center;
    margin: 10px;
}

.PartElement .add {
    margin: 5px 10px;
}

.component h2 {
    margin-top: 0px;
    margin-left: 10px;
    margin-right: 10px;
}

.component input {
    display: block;
}

.component input[type=text], .component input[type=number] {
    min-width: 33%;
    width: fit-content;
    border: transparent;
    padding: 1px 3px;
    margin: 5px 10px;
    border-bottom: black solid 1px;
    transition: .4s;
    font-size: 16px;
}
.component input[type=text]:focus, .component input[type=number]:focus {
    border-bottom-color: #0064ad;
    box-shadow: 0px 2.5px 0px 0px #0064ad;
}
.component span {
    padding: 0 10px;
    margin: 0;
    width: calc(100% - 20px);
}

textarea:focus,
input:focus,
span:focus,
select:focus {
    outline: none;
}

.component select {
    border-radius: 8px;
    border: #0064ad 1px solid;
    padding: 5px 15px;
    margin: 0 10px;
}

button.delete {
    padding: 5px 15px;
    border-radius: 8px;
    border: #ad0000 1px solid;
    background-color: transparent;
    color: #ad0000;
}

button.delete:hover {
    border: #ad0000 1px solid;
    background-color: #ad0000;
    color: white;
}

button.green {
    padding: 5px 15px;
    border-radius: 8px;
    border: #00ad25 1px solid;
    background-color: transparent;
    color: #00ad25;
}

button.green:hover {
    border: #00ad25 1px solid;
    background-color: #00ad25;
    color: white;
}
</style>