<template>
    <div class="MaintenanceElement">
        <div class="wrap">
            <span>Содержание</span>
            <button @click="deleteElement()" class="delete">Удалить</button>
        </div>
    </div>
</template>
  
<script>

export default {
    name: 'MaintenanceElement',
    props:{
        parrent : Array,
        el: Object,
    },
    data(){
        return {
            content : { 
                type : (this.$props.el.type != undefined) ? this.$props.el.type : '',
            }
        }
    },
    methods:{
        deleteElement(){
            this.$props.parrent.splice(this.$.vnode.key, 1)
        },
        sendData(){
            this.$emit('content', this.content);
        }
    },
}
</script>

<style>
    .MaintenanceElement .wrap{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 10px;
    }
    .MaintenanceElement span {
        font-weight: bold;
        font-size: 16px;
    }
</style>