<template>
    <div class="ImgElement">
        <div class="content">
            <img :src="image">
            <p>Изображение</p>
            <input type="file" @change="sendData()" :value="content.src">
        </div>
        <div class="del">
            <button @click="deleteElement()" class="delete">Удалить</button>
        </div>
    </div>
</template>
  
<script>

export default {
    name: 'ImgElement',
    data(){
        return{
            content: {
                src: (this.$props.el.src != undefined) ? this.$props.el.src : '',
            }
        }
    },
    props:{
        parrent : Array,
        el: Object,
    },
    methods:{
        deleteElement(){
            this.$props.parrent.splice(this.$.vnode.key, 1)
        },
        sendData(){
            this.$emit('content', this.content);
        }
    },
    computed:{
        image(){
            if(this.content.src != ''){
                return require(this.content.src);
            }else{
                return require('@/assets/no-image.jpg');
            }
        }
    }
}
</script>

<style scoped>

img{
    width: 400px;
    height: 400px;
}
.content { 
    width: 400px;
    margin: 10px auto;
}
.del {
    margin: 0 10px;
    display: flex;
    justify-content: flex-end;
}
button.delete{
    margin-left: auto;
}

</style>