<template>
    <div class="HeaderElement">
        <div class="wrapper">
            <input class="header" type="text" @change="sendData()" v-model="content.text">
            <select v-model="content.type" @change="sendData()">
                <option value="h1">Главный заголовок</option>
                <option value="h2">Подзаголовок</option>
            </select>
            <button @click="deleteElement()" class="delete">Удалить</button>
        </div>
    </div>
</template>
<script>

export default {
    name: 'HeaderElement',
    data(){
        return{
            content: {
                text : (this.$props.el.text != undefined) ? this.$props.el.text : '',
                type : (this.$props.el.type != undefined) ? this.$props.el.type : 'h1', 
            }
        }
    },
    props:{
        parrent : Array,
        el: Object,
    },
    methods:{
        deleteElement(){
            this.$props.parrent.splice(this.$.vnode.key, 1)
        },
        sendData(){
            this.$emit('content', this.content);
        }
    },
}

</script>

<style>
    .component input.header{
        border: transparent;
        padding: 1px 3px;
        margin: 5px 10px;
        border-bottom: black solid 1px;
        color: black;
        font-weight: bold;
        text-align: center;
        width: 100%;
    }
    .component .wrapper{
        margin: 0 10px;
        display: flex;
        justify-content: center;
    }
</style>