<template>
    <div class="component">
        <textarea v-model="content.text" @loadeddata="loaded()" @change="sendData()"></textarea>
        <button @click="deleteElement()" class="delete">Удалить</button>
    </div>
</template>
<script>

export default {
    name: 'ParagraphElement',
    data(){
        return{
            content: {
                text : (this.$props.el.text != undefined) ? this.$props.el.text : '',
            }
        }
    },
    props:{
        parrent : Array,
        el: Object ,
    },
    methods:{
        deleteElement(){
            this.$props.parrent.splice(this.$.vnode.key, 1)
        },
        sendData(){
            this.$emit('content', this.content);
        },
        loaded(){
            this.content.text = (this.$props?.el != undefined)? JSON.parse(JSON.stringefy(this.$props.el)).content.text : '';
        }
    }
}
</script>
<style scoped>
textarea{
    width: calc(100% - 20px);
    min-height: 230px;
    resize: none;
    overflow: visible;
    border: transparent;
    margin: 0 10px;
}
button.delete{
    margin: 0 10px;
}
</style>