<template>
    <div class="auth reg">
        <div class="wrapper">
            <div class="reg-form">
                <div class="img">
                    <img alt="site logo" src="../assets/big-logo.png">
                </div>
                <input @keyup="validate()" :class="{wrong : !validation.login}" type="text" v-model="value.login" placeholder="Логин" required>
                <div class="error-message" v-if="!validation.login"> Логин должен состоять из английских букв и цифр </div>

                <input @keyup="validate()" :class="{wrong : !validation.email}" type="email" v-model="value.email" placeholder="Адрес электронной почты" required>
                <div class="error-message" v-if="!validation.email"> Адрес электронной почты должен соответствовать маске: name@mail.ru </div>

                <input @keyup="validate()" :class="{wrong : !validation.name}" type="text" v-model="value.name" placeholder="Имя" required>
                <div class="error-message" v-if="!validation.name"> Имя должно состоять только из букв кириллицы, первая буква заглавная </div>

                <input @keyup="validate()" :class="{wrong : !validation.surname}" type="text" v-model="value.surname" placeholder="Фамилия" required>
                <div class="error-message" v-if="!validation.surname"> Фамилия должна состоять только из букв кириллицы, первая буква заглавная  </div>

                <input @keyup="validate()" :class="{wrong : !validation.password}" :type="type_passSeen" v-model="value.password" placeholder="Пароль" required> 
                
                <button class="visibility" v-if="!passSeen" @click="hide(1)"><img src="@/assets/eye.svg"></button>
                <button class="visibility" v-if="passSeen" @click="hide(1)"><img src="@/assets/eye-slash.svg" alt=""></button>
                
                <div class="error-message" v-if="!validation.password"> <p>Ваш пароль не соответствует правилам!</p> В нем должны быть латинские символы, как минимум одна цифра, спец символы (допустимые: !@#$%^&*_) и минимум 8 символов. </div>

                <input @keyup="validate()" :class="{wrong : !validation.password_repeat}" :type="type_passSeen_repeat" v-model="value.password_repeat" placeholder="Повтор пароля" required> 
                
                <button class="visibility" v-if="!passSeen_repeat" @click="hide(2)"><img src="@/assets/eye.svg"></button>
                <button class="visibility" v-if="passSeen_repeat" @click="hide(2)"><img src="@/assets/eye-slash.svg" alt=""></button>

                <div class="error-message" v-if="!validation.password_repeat"> Пароли должны совпадать! </div>
                
                <div class="loading-img" v-if="loading" ><img src="@/assets/loading.gif"></div>

                <div class="error-message" v-if="registred"> Аккаунт с такими данными уже зарегестрирован! </div>
                
                <button v-if="!loading" @click="registrate()">Зарегистрироваться</button>
                <p v-if="!loading"><router-link to="/login">Войдите</router-link>, если у вас есть аккаунт</p>
            </div>
        </div>
    </div>
</template>
<script>
import Lib from '@/Library';
export default{
    data(){
        return{
            validation : {
                login : true, 
                email : true, 
                name : true, 
                surname : true, 
                password : true, 
                password_repeat : true, 
            },
            value : {
                login : '', 
                email : '', 
                name : '', 
                surname : '', 
                password : '', 
                password_repeat : '', 
            },
            regex : {
                login : /^[\dA-Za-z0-9]+$/, 
                email : /^[\w0-9]+@([\w-]+\.)+[\w-]{2,4}$/, 
                name : /^[А-Я][а-я]+$/, 
                surname : /^[А-Я][а-я]+$/, 
                password : /^(?=.*[\d])(?=.*[!@#$%^&*])[\w!@#$%^&*]{8,}$/, 
                password_repeat : /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/, 
            },
            loading : false,
            passSeen : true,
            passSeen_repeat : true,
            type_passSeen : 'password',
            type_passSeen_repeat : 'password',
            registred: false
        }
    },
    methods:{
        validate(){
            console.clear();
            for (const key in this.$data.validation) {
                this.$data.validation[key] = (this.$data.value[key] !== '' && this.$data.value[key].match(this.$data.regex[key]) != undefined);
            }
            this.validation.password_repeat = this.value.password == this.value.password_repeat;
        },
        registrate(){
            let correct = this.$data.value.login.length != 0;
            this.validate();
            for (const key in this.$data.validation) {
                correct = correct && this.$data.validation[key];
            }
            if(correct){
                this.loading = true;
                (async ()=>{
                    let api = new Lib.Api();
                    let result = await api.reg(
                        this.value.name, 
                        this.value.surname, 
                        this.value.email, 
                        this.value.login, 
                        this.value.password, 
                        ""
                    );
                    if(result == 1){
                        window.location.href = './login';
                        this.registred = false;
                    }else if(result == 0){
                        this.loading = false;
                        this.registred = true;
                    }else{
                        this.loading = false;
                        console.log(result);
                    }
                })()
            }else{
                this.loading = false;
            }
        },
        hide(val){
            if (val == 1) {
                this.$data.passSeen = !this.$data.passSeen;
                this.$data.type_passSeen = (this.$data.passSeen) ? 'password' : 'text';
            }else{
                this.$data.passSeen_repeat = !this.$data.passSeen_repeat;
                this.$data.type_passSeen_repeat = (this.$data.passSeen_repeat) ? 'password' : 'text';
            }
        }
    }
}
</script>

<style>
    .auth{
        background-color: #ced4da;
        width: 100%;
    }
    .reg{
        min-height: calc(100dvh - 170px);
        height: fit-content;
        padding: 50px 0 10px 0;
    }
    .auth .wrapper{
        background-color: white;
        border-radius: 8px;
        width: 450px;
        margin: 0 auto;
        padding: 50px;
    }
    .auth .img img{
        width: 100%;
    }
    .auth .img{
        width: 150px;
        margin: 0 auto;
    }
    .auth .reg-form input{
        display: block;
        width: calc(100% - 32px);
        padding: 8px 16px;
        font-size: 18px;
        font-family: Arial, Helvetica, sans-serif;
        border-radius: 8px;
        border: 1px solid #8f959e;
        color: rgb(73, 80, 87);
        margin: 20px 0;
        transition: .4s;
    }
    .auth .reg-form p {
        margin-top: 10px;
    }
    .auth .reg-form a{
        color: #0064ad;
        border-bottom: 2px transparent solid;
        text-decoration: none;
        transition: .4s;
        padding-bottom: 2px;
    }
    .auth .reg-form a:hover{
        border-bottom-color: #0f6dbf;
    }
    .auth button{
        margin-top: 40px;
        color: white;
        background-color: #0064ad;
        padding: 8px 16px;
        display: block;
        width: fit-content;
        border-radius: 8px;
        border: transparent 1px solid;
        font-size: 20px;
        margin-bottom: 15px;
    }
    .auth button:hover{
        background-color: #5babf2;
    }
    .auth .reg-form input:focus{
        border: 1px solid transparent;
        box-shadow: 0 0 0 0.2rem #0f6dbf;
    }
    .auth button.visibility{
        padding: 0;
        margin: 0;
        height: 41px;
        width: 41px;
        background: transparent;
        position: absolute;
        margin-top: -60px;
        margin-left: 410px;
    }
</style>