<template>
    <div class="component">
        <div class="table-content">
            <p>Таблица # - <input type="text" style="display:inline;" @change="sendData()" v-model="content.name"></p>
            <table>
                <tr>
                    <th class="first"></th>
                    <th class="first-row" v-for="(th, j) in content.table[0].length" v-bind:key="j" ><div class="wrap"><button class="removeRow" @click="delCol(j)">—</button></div></th>
                </tr>
                <tr>
                    <th class="first"></th>
                    <th v-for="(th, j) in content.table[0]" v-bind:key="j" contenteditable="true" ref="cells" @keyup="cellInput(0, j)">{{ th }}</th>
                </tr>
                <tr v-for="(tr, i) in genArr(content.table)" v-bind:key="i">
                    <th class="first"><div class="wrap"><button @click="delRow(i+1)" class="removeRow">—</button></div></th>
                    <td v-for="(td, j) in tr" v-bind:key="j" contenteditable="true" ref="cells" @keyup="cellInput(i+1, j)">{{ td }}</td>
                </tr>
            </table>
            <button @click="addTr()" class="green">Добавить строку</button>
            <button @click="addTd()" class="green">Добавить столбец</button>
            <button @click="deleteElement()" class="delete">Удалить</button>
        </div>
    </div>
</template>
  
<script>

export default {
    name: 'TableElement',
    data(){
        return{
            content: {
                name: (this.$props.el.name != undefined) ? this.$props.el.name : '',
                table : (this.$props.el.table != undefined) ? this.$props.el.table : [
                    ['Заголовки', ''],
                    ['', ''],
                ],
            },
            renderComponent: true,
        }
    },
    props:{
        parrent : Array,
        el: Object,
    },
    methods:{
        deleteElement(){
            this.$props.parrent.splice(this.$.vnode.key, 1)
        },
        addTr(){
            this.content.table.push([]);
            this.content.table[0].forEach(() => {
                this.content.table[this.content.table.length - 1].push('');
            });
            this.$forceUpdate();
        },
        addTd(){
            this.content.table.forEach(el => {
                el.push('')
            });
            this.$forceUpdate();
        },
        genArr(arr){
            let out = []
            for (let i = 1; i < arr.length; i++) {
                const el = arr[i];
                out.push(el);
            }
            return out;
        },
        delRow(row){
            this.content.table.splice(row, 1);
            this.$forceUpdate();
        },
        delCol(col){
            this.content.table.forEach(e => {
                e.splice(col, 1);
            });
            this.$forceUpdate();
        },
        sendData(){
            this.$emit('content', this.content);
        },
        showRefs(){
            console.log(this.$refs.cells);
        },
        cellInput(i, j){
            this.content.table[i][j] = this.$refs.cells[i*this.content.table[0].length+j].innerText;
            console.log(this.$refs.cells[i*this.content.table[0].length+j].innerText);
            console.clear();
            this.sendData();
        },
    }
}
</script>
<style scoped>
.component .table-content{
    padding: 10px 20px;
    overflow-x: auto;
}
.component .table-content button.green{
    margin-top: 20px;
    margin-right: 20px;
}
.component .table-content p{
    font-size: 16px;
    padding-left: 20px;
}
.component .table-content input{
    margin: 0;
    margin-bottom: 20px;
}
.component table{
    width: 100%;
    border-collapse: collapse;
}
.component table th{
    border: 1px solid black;
    border-bottom: double black 1px;
    padding: 10px;
    text-align: center;
}
.component table th.first{
    padding: 0;
    margin: 0;
    border: none;
    width: 30px;
}
.component table th.first-row{
    padding: 0;
    margin: 0;
    padding-bottom: 10px;
    border: none;
    width: fit-content;
}
.component table th.first .wrap{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: left;
}
.component table th.first-row .wrap{
    display: flex;
    align-items: center;
    justify-content: center;
}
.component table tr{
    width: 100%;
}
.component table td{
    border: 1px solid black;
    padding: 10px;
}
table button.removeRow{
    width: 20px;
    height: 20px;
    border: 1px solid red;
    color: red;
    background: transparent;
    border-radius: 50%;
    margin: 0;
    display: block;
    font-weight: bold;
}
table button.removeRow:hover{
    border: 1px solid red;
    color: white;
    background: red;
}

</style>