<template>
    <div class="component">
        <h3>Информация о документе</h3>
        <span>Группа</span>
        <input type="text" v-model="content.group" @change="sendData()">
        <span>Номер в списке</span>
        <input type="number" min="1" max="50" v-model="content.listNumber" @change="sendData()">
        <span>Тип документа</span>
        <input type="text" v-model="content.type" @change="sendData()">
        <span>Преподаватель</span>
        <input type="text" v-model="content.teacher" @change="sendData()">
        <span>Код предмета</span>
        <input type="text" v-model="content.subject" @change="sendData()">
        <span>Полное название предмета</span>
        <input type="text" v-model="content.subjectFull" @change="sendData()">
    </div>
</template>

<script>
export default {
    name: 'InfoAboutDocument',
    data(){
        return{
            content: {
                group: (this.$props.el.group != undefined) ? this.$props.el.group : '',
                listNumber: (this.$props.el.listNumber != undefined) ? this.$props.el.listNumber : '',
                type: (this.$props.el.type != undefined) ? this.$props.el.type : '',
                teacher: (this.$props.el.teacher != undefined) ? this.$props.el.teacher : '',
                subject: (this.$props.el.subject != undefined) ? this.$props.el.subject : '',
                subjectFull: (this.$props.el.subjectFull != undefined) ? this.$props.el.subjectFull : '',
            }
        }
    },
    props:{
        parrent : Array,
        el: Object,
    },
    methods : {
        sendData(){
            this.$emit('content', this.content);
        }
    }
}
</script>

<style scoped>
    .component{
        padding: 10px;
        padding-top: 0px;
    }
</style>
